svg#bsbSlider {
    fill: none;
}

.bsbGeneralMainArea {

    .components-base-control__field {
        flex-direction: column;
    }

    .bsbToggle {
        .components-base-control__field {
            flex-direction: row !important;
            margin-top: 15px;
            justify-content: space-between !important;
        }
    }

    label {
        display: block;
    }

    .bsbButtonArea {
        display: flex;
        align-items: center;
        column-gap: 10px;
    }

    .bsbImageUpload {
        font-size: 16px;
        border-radius: 3px;
        background-color: #4527a4;
        color: #fff;
        margin-top: 5px;
    }

    .components-button-group {
        border-radius: 3px;

        button.is-primary {
            background: #000080 !important;

            &:hover {
                background: #002fa7 !important;
            }
        }
    }

}

.bsbGeneralMainArea,
.bsb-tab-panel {

    label,
    span,
    p,
    span {

        font-weight: 500;
    }
}

.bsbCarousel {
    .block-editor__container {
        img {
            height: 100% !important;
        }
    }

}

.bsb-tab-panel {
    p {
        margin-top: 0px !important;
    }

    button.is-primary {
        background: #000080 !important;

        &:hover {
            background: #002fa7 !important;
        }
    }

    .bsbWHControl {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        margin-top: -15px;
        margin-bottom: 10px;
    }

    .marginLeft {
        margin-bottom: 0px !important;
    }

    .warning {
        font-size: 12px;
        font-weight: 400;
    }
}

.bPlBtnGroup {
    button {
        svg {
            margin-right: 0px !important;
        }
    }
}

.noticePatter {
    text-align: center;
    padding: 5px 5px;
    background: red;
    color: #fff;
    margin-top: 13px !important;
    margin-bottom: 8p
}

.bsbPatters {
    position: relative;
    margin-bottom: 5px;

    img {}

    .overlyButton {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: 0.3s;
        background-color: #1e1b1b94;

        button {
            border: none;
            font-size: 12px;
            border-radius: 2px;
            padding: 4px 15px;
            font-weight: 500;
            text-transform: uppercase;
            cursor: pointer;
            transition: 0.3s;

            &:hover {
                color: #fff;
                background-color: #007cba;
                transition: 0.3s;

            }
        }
    }

    &:hover .overlyButton {
        opacity: 1;
        transition: 0.3s;
    }
}

.labelPro {
    padding: 0px 5px;
    border-radius: 5px;
    color: #fff;
    background-color: #4527a4;
}

.bsbProFeature {
    h3 {
        a {
            background-color: #4527a4 !important;
            margin: 0 5px;
            text-decoration: none;
            color: #fff;
            padding: 2px 1px;
            font-weight: 500;
            font-size: 15px;
        }
    }

    >a {
        background: #4527a4 !important;
        border: 1px solid #4527a4 !important;
        box-shadow: 1px 1px 0px 1px #4527a4 !important;
        padding: 5px 10px !important;
        border-radius: 3px !important;
        font-size: 14px !important;
        text-decoration: none !important;
        color: #fff;
        cursor: pointer;
    }
}